<template>

  <!-- <div class="start" :class="{ end : 모달창열렸니 }"> -->
    <!-- 모달창 -->
    <!-- </div> -->
  <transition name="fade">
    <ModalWindow @closeModal ="모달창열렸니 = false" v-bind:원룸들="원룸들" :누른거="누른거" :모달창열렸니="모달창열렸니"/>
  </transition>
  



  <div class="menu">
    <a v-for="작명 in menu" :key="작명"> {{작명}} </a>
  </div>

  <DiscountItem :할인률="할인률" v-if="showDiscount == true" />


  <button @click="priceSort">From Low Pirce</button>
  <button @click="abcSort">A-Z Sort</button>
  <button @click="cbaSort">Z-A Sort</button>
  <button @click="sortBack">Back to Start</button>
  <ItemCard @openModal="모달창열렸니 = true; 누른거 = $event" :원룸들="원룸들"/>

  
</template>

<script>
import roomData from './assets/oneroom.js';
import Discount from './Discount.vue';
import ModalWindow from './ModalWindow.vue';
import ItemCard from './ItemCard.vue';

export default {
  name: 'App',
  data(){
    return{
      할인률 : 30,
      showDiscount : true,
      원룸들오리지널 : [...roomData],
      누른거 : 0,
      원룸들 : roomData,
      모달창열렸니 : false,
      신고수 : [0,0,0],
      price1 : 60,
      price2 : 70,
      menu : ['Home', 'Shop', 'About'],
      products : ['역삼동원룸', '천호동원룸', '마포구원룸'],
    }
  },
  methods : {
    increase(){
      this.신고수 += 1;
    },
    priceSort(){
      this.원룸들.sort(function(a,b){
        return a.price - b.price;
      })
    },
    sortBack(){
      this.원룸들 = [...this.원룸들오리지널];
    },
    abcSort(){
      this.원룸들.sort(function(a, b) {
        if (a.title > b.title) return 1;
        if (a.title < b.title) return -1;
        return 0;
      })
    },
    cbaSort(){
      this.원룸들.sort(function(a, b) {
        if (a.title > b.title) return -1;
        if (a.title < b.title) return 1;
        return 0;
      })
    },
  },

  mounted(){
    setInterval(()=>{
      this.할인률--;
      if(this.할인률 <= 0){
        this.showDiscount = false;
      }
    }, 1000)
  },

  components: {
    DiscountItem : Discount,
    ModalWindow : ModalWindow,
    ItemCard,
  },
}
</script>

