<template>
    <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <img :src="원룸들[누른거].image" class="room-image">
      <Discount/>
      <h4>{{원룸들[누른거].title}}</h4>
      <p>{{원룸들[누른거].content}}</p>
      <input v-model="month">
      <p>{{ month }} months : $ {{ 원룸들[누른거].price * month }}</p>
      
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>  
  </template>
  
  <script>
  export default {
      name : "ModalWindow",
      data(){
        return{
          month : 3,
        }
      },
      watch : {
        month(a){
          if(a >= 13){
            alert('Write under 13 only');
            this.month = 3;
          }
          if(isNaN(a) == true){
            alert('Write Number only');
            this.month = 3;
          }
        }
      },
      props : {
          원룸들 : Array,
          누른거 : Number,
          모달창열렸니 : Boolean,
      },
      beforeUpdate() {
        if(this.month <= 2){
          alert("We sell from 3months");
          this.month = 3;
        }
      },
  }
  </script>
  
  <style>
  
  </style>